            },
            drawerFilter: false,
            showDialogCart:false,
            badgeShow:false,
            loader: null,
            loading: false,
            currencys:[],
            icons: [
                'mdi-facebook',
                'mdi-twitter',
                'mdi-linkedin',
                'mdi-instagram',
            ],
            showDialogStoreAdd: false,
            mealIDShow: null,
            columuns:4,
            showNew:false,
            isSearch:true,
            isLoadingSearch: true,
            search: null,
            show:false,
            mealPeoShow:0,
            postUserId: null,
            meals: [],
            mealName:null,
            pagination: {
                current: 1,
                total: 0
            },
        }
    },
    beforeDestroy () {
        window.removeEventListener('scroll', this.onScroll)
    },

    methods: {
        onScroll () {
        if (window.pageYOffset < 0) {
            return
        }
        if (Math.abs(window.pageYOffset - this.lastScrollPosition) < OFFSET) {
            return
        }
        this.showNavbar = window.pageYOffset < this.lastScrollPosition
        this.lastScrollPosition = window.pageYOffset
        },
        applyFilters(){
            this.isLoadingSearch = true;
            var search
            if(this.search == null){
                search = 'null'
            }else{
                search = this.search
            }
            this.axios.post('/getPagmMalsSearch/'+search, {filters: this.filters})
                .then(response => {
                    this.meals = response.data.data;
                    this.isSearch = false;
                    this.pagination.current = response.data.current_page;
                    this.pagination.total = response.data.last_page;
                    this.isLoadingSearch = false;
                })
                .catch(() => {
                    this.meals = null;
                    this.isSearch = true;
                    this.isLoadingSearch = false;
                });

        },
        resetFilters(){
            this.filters.sort='low price',
            this.filters.price='$$$$$',
            this.filters.tags=[],
            this.filters.allergies=[],
            this.filters.options=[],
            this.filters.mealTypes=[],
            this.filters.cuisines=[],
            this.applyFilters();
        },
        removeTag (item) {
            this.filters.tags.splice(this.filters.tags.indexOf(item), 1)
            this.filters.tags = [...this.filters.tags]
        },
        removeAllergie (item) {
            this.filters.allergies.splice(this.filters.allergies.indexOf(item), 1)
            this.filters.allergies = [...this.filters.allergies]
        },

        removeMealType (item) {
            this.filters.mealTypes.splice(this.filters.mealTypes.indexOf(item), 1)
            this.filters.mealTypes = [...this.filters.mealTypes]
        },
        removeCuisine (item) {
            this.filters.cuisines.splice(this.filters.cuisines.indexOf(item), 1)
            this.filters.cuisines = [...this.filters.cuisines]
        },
        removeOption (item) {
            this.filters.options.splice(this.filters.options.indexOf(item), 1)
            this.filters.options = [...this.filters.options]
        },
        updateCurrency(){
            this.axios.get('/setCurrency/'+this.filters.currency)
            .then(() => (this.getMeals()))
        },
        modfShowDialogAdd(mealId,mealPeo,mealName){
            this.showDialogStoreAdd=true;
            this.mealIDShow=mealId;
            this.mealPeoShow=mealPeo;
            this.mealName=mealName;
            //console.log(this.mealIDShow);
        },
        getMeals() {
                this.axios.get('/getPagmMals?page=' + this.pagination.current)
                    .then(response => {
                        //console.log(response.data.currency_id);
                        this.meals = response.data.meals.data;
                        this.filters.currency =response.data.currency_id;
                        this.isSearch = false;
                        this.isLoadingSearch = false;
                        this.pagination.current = response.data.current_page;
                        this.pagination.total = response.data.last_page;
                    });
            },
            onPageChange() {
                this.getMeals();
            }
        },
    mounted() {
        this.lastScrollPosition = window.pageYOffset
        window.addEventListener('scroll', this.onScroll)
        const viewportMeta = document.createElement('meta')
        viewportMeta.name = 'viewport'
        viewportMeta.content = 'width=device-width, initial-scale=1'
        document.head.appendChild(viewportMeta)

        this.getMeals();
        this.axios
            .get('getOptions')
            .then(response => (this.optionsItems = response.data));
        this.axios
            .get('getMealType')
            .then(response => (this.mealTypesItems = response.data));
        this.axios
            .get('getCuisines')
            .then(response => (this.cuisinesItems = response.data));
        this.axios
            .get('getCurrencyArr')
            .then(response => (this.currencys = response.data));
        this.axios
            .get('getTags/2')
            .then(response => (this.filters.tags = response.data, this.tagsItems = response.data ));
        this.axios
        .get('getIngredients')
            .then(response => (this.allergiesItems = response.data));

    },
    changeShow: function(idx) {
      this.list[idx].show = !this.list[idx].show;
    },

    computed: {
        currency_x() {
        return this.filters.currency;
        }
    },
    watch: {
        loader () {
            const l = this.loader
            this[l] = !this[l]

            setTimeout(() => (this[l] = false), 3000)

            this.loader = null
        },
        search (val){
            //console.log(val.length);
            if (val.length < 1) return this.getMeals();
            this.applyFilters();



        },
        currency_x (){
            this.updateCurrency()
            this.isSearch = true;
            this.isLoadingSearch = true;
        }
    }
}
</script>


